.sub-header {
    background-color: #f1ebeb;
    height: 336px;
    margin-top: 0px;
    text-align: left;
    padding: 10px 0px 68px 125px;
}
.sub-header h1 {
    font-size: 51px;
    padding: 14px;
    line-height: 60px;
}
.sub-header p {
    font-size: 25px;
    padding: 0px 15px;
    font-family: arial;
    margin-bottom: 16px;
}
.sub-header button {
    padding: 12px 21px;
    border: navajowhite;
    background-color: #4997D3;
    color: #fff;
    font-size: 22px;
    border-radius: 3px;
    margin-left: 22px;
}