.cards {
    display: grid;
    column-gap: 94px;
    grid-template-columns: repeat(3,1fr);
}
.tutorials h2 {
    font-size: 34px;
    color: #4997D3;
    margin-bottom: 0px;
}
.tutorials p {
    text-align: justify;
    padding: 5px;
    line-height: 23px;
    font-size: 15px;
}
.tutorials button {
    padding: 12px 10px;
    font-size: 18px;
    background-color: #b9e6f1;
    color: #2a2f2b;
    border: navajowhite;
}
main{
	display: flex;
}
.RightSide {
    background-color: #F5F5F5;
    width: 600px;
    padding: 0px 35px 15px 35px;
    text-align: justify;
}
